.list-element {
	cursor: pointer;
}

.photo {
	width: 100%;
	height: 100%;
	border-radius: 7px;
	/*	box-shadow: 3px 3px 5px 6px #ccc;*/
	display: block;
}

.overlay {
	/*Positioning it to cover the parent*/
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;

	visibility: hidden;
	transition: 0.5s ease;
	width: 100%;
	height: 100%;
	color: white;
	opacity: 0;
	border-radius: 7px;
	font-weight: bold;

	/*Positioning the elements inside*/
	display: flex;
	justify-content: space-between;
}

.list-element:hover .overlay {
	visibility: visible;
	background-color: #000;
	opacity: 0.7;
}

.delete {
	color: red;
	border: 1px solid red;
	background: none;
	height: 30px;
	width: 80px;
	margin: 5px;
	border-radius: 10px;
	font-weight: bold;
	padding: none;
	font-size: 13px;
	cursor: pointer;
	transition: 0.5s ease;
}

.delete:hover {
	background: red;
	color: white;
}

@media screen and (max-width: 600px) {
	.overlay {
		font-size: 11px;
	}
	.delete {
		font-size: 11px;
		width: 50px;
		height: 20px;
	}
}
